import common from '~/mixins/common'
export default {
  props: {
    mode: String,
    crate: Number
  },
  mixins: [common],
  computed: {
    keycardsRequired () {
      const mode = this.tracker.mode
      const conquest = this.conquest[mode]
      const crateSelected = this.tracker[mode].crate
      return conquest.crates[`${mode}-0${crateSelected}`].keycards
    }
  }
}
